     shoppnav,
     shoppdibu
  },
  methods:{
    ff(i){
      console.log("index",i);
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

 body{
   background: #f5f5f5;
  
 }

 body,html{
   height: 100%
 }

  .ding{
    height: 1.3rem;

  }
  .zhong{
    flex:1;
    overflow-y: auto;
  }



</style>


