<template>
     
        <div class="zhong_da" style="display:flex;-webkit-flex-wrap:wrap;width:3.75rem">
        <div class="zhong" v-for="n in data" :key="n.id">
            <!-- <img class="xiangqing" src="@/assets/img/xiangqing.jpg"/> -->
            <img class="xiangqing" :src="n.imgUrl"/>
            <div style="display:flex; flex-direction:column;width:1.86rem;">
                <span style="font-size:.14rem;width:1.86rem;" :value="n.imgTitle ">{{n.imgTitle}}</span>
                <span style="font-size:.13rem; margin-left: 1.1rem;" >{{n.imgType}}</span>
                <span style="text-decoration:line-through;margin-top: -5px; font-size:.13rem;width:1.86rem;" :value="n.price">{{n.price}}</span>
                <span style="font-size:.13rem;width:1.86rem;" :value="n.yajin">{{n.yajin}}</span>
                <img class="gouwuche" :src="n.img"/>
            </div>
        </div>
 
 
 </div>



     
  
</template>

<script>
export default {
    props:["data"],
  name: 'shoppnav',
  data () {
    return {
     
    }
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.zhong_da{
       display: flex;
   }

    .zhong_da .zhong{
        width:1.86rem;
      
        margin-top: 23px;
       

     
    }
    .zhong img{
        display: inline-block;
        
    }
   .zhong .xiangqing{
       display: inline-block;
       width: 1.76rem;
   
       
   }

   .zhong .gouwuche{
       display: block;
      width: 1.8rem;
    
      
   }

   .zhong span{
       width:.6rem;
       margin-top: .09rem;
   }

    .zhong span:nth-child(3){
        color:#9f9f9f;
    }
        .zhong span:nth-child(4){
        color:#9f9f9f;
    }
        .zhong span:nth-child(5){
        color:#e41f4c;
    }


</style>


