<template>
 <div class="applunbo">

           
        <div class="swiper-container">
  <div class="swiper-wrapper">
    <div class="swiper-slide">
        
       <img src="@/assets/img/lunbotu.jpg"/>
    </div>
    <div class="swiper-slide">
        <img src="@/assets/img/u=3003577339,2568304884&fm=26&gp=0.jpg"/>
    </div>
    <div class="swiper-slide">  <img src="@/assets/img/u=90516320,1594580616&fm=26&gp=0.jpg"/>
    </div>
  </div>
</div>

 </div>
     
     
    
    
  
</template>
    
<script>
    import Swiper from '../../../bower_components/swiper/dist/js/swiper.js'
    import '../../../bower_components/swiper/dist/css/swiper.css'

    export default {
    name: 'applunbo',
    data () {
        return {
        
                }
    },
    mounted(){
        new Swiper('.swiper-container', {
           autoplay: {
            delay: 3000,
             stopOnLastSlide: false,
            disableOnInteraction: true,
    },
        });
    }

    }

</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
          
          .applunbo{
              width: 3.75rem;
              
          }


         .swiper-wrapper{
              margin:0 auto;
         
        } 
        .swiper-slide{
            width:3.75rem;
        }

       

        .swiper-wrapper img{
           
            display: inline-block;
            width: 3.8rem;
            height: 2.5rem;
          
        }


</style>


