import Vue from 'vue'
import Router from 'vue-router'
import index  from  '@/page/index.vue'
import shoppStop  from  '@/page/shoppStop.vue'
import zhuceq  from  '@/page/zhuce.vue'
import zhuce_1  from  '@/components/zhuce/denglu/zhuce_1'
import denglu_1 from '@/components/zhuce/denglu/denglu_1'
import shoppliebiao from '@/page/shoppliebiao.vue'
Vue.use(Router)

export default new Router({
  routes: [
    {path:"/zhuce",redirect:"/components/zhuce/denglu"},
    {
      path:'/components/zhuce/denglu',
      component:zhuceq,
      children:[
        {path:"",component:zhuce_1},
        {path:"zhuce_1",component:zhuce_1},
        {path:"denglu_1",component:denglu_1}
      ]

    },
    {
      path: '/',
      name: 'index',
      component: index
    },
    {
      path:'/shoppStop',
      name:'shoppStop',
      component:shoppStop

    },{
      path:'/shoppliebiao',
      name:'/shoppliebiao',
      component:shoppliebiao
    }
    
   

  ]
})



