<template>
  <div class="zhong">
      <ul class="diyibu">
         <li  v-for="(u) in data" :key="u.id" @click="dianclick(key)" >{{u.liebiaotou}}</li>
        


      </ul>

        <div class="dierbu" >
         <ul v-for="n in data" :key="n.id" >
            <li  v-for="(m,key) in n.jiaxiangweidao" :key="key" >{{m.jiaxiangweidao_2}}
           
            </li>
  



          </ul>



        </div>

  </div>
  
   
</template>
<script>
export default {
  name: 'liebiaozhong',
   props:["data"],
  data () {
    return {
     
    }
  },
  methods:{
    dianclick(key){
      this.id = key;
    }
  }
}
</script>
<style scoped>

.diyibu{
  overflow: hidden;
}
.zhong{
  display: flex;
  margin-top: .35rem;
}
  .diyibu{
    width:1.05rem;
    height:4.94rem ;
  }

  .diyibu li{
    width: 1.05rem;
    height: .5rem;
    border-bottom:1px solid #ececec; 
    font-size: .14rem;
    font-weight: 600;
    text-indent: .25rem;
    line-height: .6rem;
  }

  .dierbu{
    width: 3.75rem;
     height:4.94rem ;
  }

  .dierbu li{
    font-size: .16rem;
    margin-left: .23rem;
    line-height: .45rem;
  }

</style>


