<template>
      <div class="bigBox">
        <liebiaotou></liebiaotou>
        <liebiaozhong :data="liebiao"></liebiaozhong>
       
      </div>
</template>

<script>
        import liebiaotou from '@/components/shoppliebiao/liebiaotou.vue'
         import liebiaozhong from '@/components/shoppliebiao/liebiaozhong.vue'
           import liebiaoxiangqing from 'static/data.json'
      

export default {
  name: 'shoppliebiao',
  data () {
    return {
     liebiao:liebiaoxiangqing.shangpinxiangqing
    }
  },
  components:{
   liebiaotou,
   liebiaozhong
  },
 
}
</script>

