<template>
<div class="zuidada">
  <div class="centent">
  <div class="dingdingding">
     <app-head></app-head>
     <app-nav></app-nav>
     <applunbo></applunbo>
      <app-navq></app-navq>
      <appsousuo></appsousuo>
      <appimg></appimg>
      </div>
      <div id="zhongbu_r">
      <appzhong  :data="appzhongq"></appzhong>
      </div>
  </div>
    
      <appdibu></appdibu>
      
      <!-- <input type="button" value="点击"   @click="ff(appzhongq)"/> -->
</div>
  
</template>

<script>
     
    import appHead  from  '@/components/index/appHead.vue'
    import appNav   from   '@/components/index/appNav.vue'
    import applunbo   from   '@/components/index/applunbo.vue'
    import appNavq   from   '@/components/index/appNavq.vue'
    import appsousuo from '@/components/index/appsousuo.vue'
    import appimg from '@/components/index/appimg.vue'
    import appzhong from '@/components/index/appzhong.vue'
    import IndexImg from 'static/data.json'
    import appdibu from '@/components/index/appdibu.vue'
        
    



export default {
  name: 'index',
  data () {
    return {
      // indexImg
        appzhongq:IndexImg.indexImg
    }
  },
  components:{
      appHead,
      appNav,
      applunbo,
      appNavq,
      appsousuo,
      appimg,
      appzhong,
      IndexImg,
      appdibu
  },
  methods:{
    ff(i){
      console.log("index",i);
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

  body,html{
    height:100%;

  }
  .zuidada{
    display: flex;
    flex-direction: column;
    height: 100%;
  }

  .dingdingding{
    height:8.3rem ;
  }

  .centent{
    flex:1;
    overflow-y: auto;
  }

</style>


